<script>
	import TodoItem from './TodoItem.svelte';
	import { fly } from 'svelte/transition';

	let newTodoTitle ='';
	let currentFilter = 'all';
	let nextId = 4;

	let todoList = [
		{
			id: 1,
			title: 'My first todo',
			completed: false,
		},
		{
			id: 2,
			title: 'My second todo',
			completed: false,
		},
		{
			id: 3,
			title: 'My third todo',
			completed: false,
		},
	];

	function addTodo(e) {
		if (e.key === 'Enter' && newTodoTitle) {
			todoList = [...todoList, {
				id: nextId,
				completed: false,
				title: newTodoTitle,
			}];

			nextId = nextId + 1;
			newTodoTitle = '';
		}
	}

	function checkAllTodos(e) {
		todoList = todoList.map(todo => ({
			...todo,
			completed: e.target.checked
		}));
	}

	function updateFilter(newFilter) {
		currentFilter = newFilter;
	}

	function clearCompleted() {
		todoList = todoList.filter(todo => !todo.completed);
	}

	function handleDelete(e) {
		todoList = todoList.filter(todo => todo.id !== e.detail.id);
	}

	function handleToggle(e) {
		const todoIndex = todoList.findIndex(todo => todo.id === e.detail.id);
		const updatedTodo = { ...todoList[todoIndex], completed: !todoList[todoIndex].completed};
		todoList = [
			...todoList.slice(0, todoIndex),
			updatedTodo,
			...todoList.slice(todoIndex + 1),
		];
	}

	$: filteredTodos = currentFilter === 'all' ? todoList : currentFilter === 'completed'
		? todoList.filter(todo => todo.completed)
		: todoList.filter(todo => !todo.completed)
	$: todosRemaining = todoList.filter(todo => !todo.completed).length;
</script>

<main>
	<h1>Svelte Todo App</h1>
	<input
		class="todo-input"
		placeholder="Insert todo item ..."
		bind:value={newTodoTitle}
		on:keydown={addTodo}
	>

	<div class="flex">
		<div>
			<button
				class="btn btn--filter"
				class:active={currentFilter === 'all'}
				on:click={() => updateFilter('all')}
			>
				All
			</button>
			<button
				class="btn btn--filter"
				class:active={currentFilter === 'active'}
				on:click={() => updateFilter('active')}
			>
				Active
			</button>
			<button
				class="btn btn--filter"
				class:active={currentFilter === 'completed'}
				on:click={() => updateFilter('completed')}
			>
				Completed
			</button>
		</div>
		<div>
			<button
				class="btn btn--clear"
				on:click={clearCompleted}
			>
				Clear completed
			</button>
		</div>
	</div>

	{#if filteredTodos.length}
		<div class="check-all" transition:fly="{{ y: 20, duration: 300 }}">
			<input
				type="checkbox"
				id="check-all"
				class="check-all__checkbox"
				on:change={checkAllTodos}
			>
			<label
				for="check-all"
				class="check-all__label"
			>
				Check all
			</label>
			<span class="remaining">{todosRemaining} items left</span>
		</div>
	{/if}

	{#each filteredTodos as todo}
		<TodoItem
			{...todo}
			on:delete={handleDelete}
			on:toggle={handleToggle}
		/>
	{/each}
</main>

<style>
	main {
		padding: 1em;
		max-width: 600px;
		margin: 0 auto;
	}

	h1 {
		color: #ff3e00;
		text-align: center;
		text-transform: uppercase;
		font-size: 4em;
		font-weight: 100;
		margin-bottom: 24px;
	}

	.todo-input {
		width: 100%;
		padding: 8px;
		margin-bottom: 16px;
		border: 1px solid #ccc;
		border-radius: 4px;
	}

	.btn {
		margin-bottom: 16px;
		margin-right: 4px;
		padding: 8px;
		border-radius: 4px;
		transition: background-color 0.2s, color, 0.2s
	}

	.btn--filter {
		margin-right: 4px;
		padding: 8px;
		border: 1px solid #71b2db;
	}

	.btn--clear {
		border: 1px solid #f59595;
	}

	@media only screen and (min-width: 600px) {
		.btn--clear:hover {
			background-color: #f59595;
			color: white;
		}
	}

	.btn--filter:hover,
	.active {
		background-color: #71b2db;
		color: white;
	}

	.flex {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}

	.check-all {
		display: flex;
		align-items: center;
		border-bottom: 1px solid lightgrey;
		padding-bottom: 8px;
		margin-bottom: 8px;
	}

	.check-all__checkbox {
		/* display: none; */
		cursor: pointer;
		margin-right: 8px;
	}

	.check-all__label {
		width: 100%;
		line-height: 32px;
		cursor: pointer;
		user-select: none;
	}

	.remaining {
		white-space: nowrap;
	}
</style>
