<script>
	import { createEventDispatcher } from 'svelte';
	import { fly } from 'svelte/transition';

	export let id;
	export let title;
	export let completed;

	const dispatch = createEventDispatcher();

	function deleteTodo() {
		dispatch('delete', { id });
	}

	function toggleComplete() {
		dispatch('toggle', { id });
	}
</script>

<div class="todo" transition:fly="{{ y: 20, duration: 300 }}">
	<input
		type="checkbox"
		id={`checkbox-${id}`}
		class="todo__checkbox"
		bind:checked={completed}
		on:change={toggleComplete}
	>
	<label
		for={`checkbox-${id}`}
		class="todo__label"
		class:completed={completed}
	>
		{title}
	</label>
	<button class="todo__remove" on:click={deleteTodo}>Ã—</button>
</div>

<style>
	.todo {
		display: flex;
		align-items: center;
		animation-duration: 0.3s;
	}

	.todo__checkbox {
		/* display: none; */
		cursor: pointer;
		margin-right: 8px;
	}

	.todo__label {
		width: 100%;
		line-height: 32px;
		cursor: pointer;
		user-select: none;
	}

	.todo__remove {
		width: 32px;
		height: 32px;
		background-color: transparent;
		text-align: right;
		padding: 0;
	}

	.completed {
		color: #ccc;
		text-decoration: line-through;
	}
</style>
